export default {
	data() {
		return {
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			showToTop: false
		}
	},
	computed: {
		list: function() {
			return this.$store.state.commentList
		}
	},
	methods: {
		goTopHandle() {
			// 回到顶部
			this.scrollTop = 0
			this.scrollTop = this.old.scrollTop
			this.$nextTick(() => {
				this.scrollTop = 0
			})
		},
		scrollHandle(event) {
			this.old.scrollTop = event.detail.scrollTop
			if (event.detail.scrollTop > 666) {
				this.showToTop = true
			} else {
				this.showToTop = false
			}
		}
	},
	onLoad: function() {

	}
}
